body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__hand-minute {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-hand;
  position: absolute;
  bottom: 50%;
}

.stopwatch__hand-second {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-hand;
  position: absolute;
  bottom: 50%;
}

.stopwatch--speed-up .stopwatch__hand-minute {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__hand-second {
  animation-duration: 10s;
}

.stopwatch__point {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotate-hand {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
